.profile__info {
  display: flex;
  flex-direction: column;
  gap: 12px;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 730px) {
  .profile__info {
    display: block;
    overflow: visible;
    margin-top: 26px;
    margin-bottom: 32px;
  }
}

